.dates {
    border: 2px solid $helper-frame;
    overflow: hidden;

    &__date {
        border-bottom: 2px solid $helper-frame;
        float: left;
        width: 50%;

        &--event {
            text-transform: capitalize;
        }
        &--date {
            text-transform: uppercase;
        }

        @media screen and (max-width: 768px) {
            // background-color: yellow;
            width: 100%;
        }
    }
}



