@media screen and (max-width: 1280px) {
    .navigation-list__item {
        padding-left: 40px;
        font-size: 12px;
        height: 20px;
        padding-bottom: 10px;
    }

    body {
        font-size: 14px;
    }

    .abstracts__desc {
        line-height: 24px;
    }

    .navigation-list__item::before {
        top: 66%;
    }

    .table-layout__item--first {
        font-size: 14px;
    }

    .about__text p {
        font-size: 20px;
        line-height: 30px;
    }

    .module-section__subtitle {
        font-size: 20px;
        line-height: 30px;
    }

    .organizers__organizer {
        font-size: 14px;
    }

    p {
        line-height: 24px;
    }

    .navigation__hashtag--link {
        font-size: 12px;
    }

    .module-section {
        
    }

    .module-section__title {
        font-size: 32px;
    }

    .module-section__title--secondary {
        font-size: 32px;
        line-height: 100%;
    }

    .module-section__text--large {
        font-size: 20px;
        line-height: 30px;
    }

    .module-section__text--large p {
        font-size: 20px;
        line-height: 30px;
    }

    .module-section__text--large {
        font-size: 20px;
        line-height: 30px;
    }

    .module-section__text--large {
        font-size: 20px;
        line-height: 30px;
    }

    .module-section a {
        padding-right: 0px;
    }

    .module-section__text--large.mt2 a {
        padding-right: 25px;
    }

    .module-section__text--large a {
        padding-right: 25px;
    }

    .module-section a {
        &:after {
            right: 8px;
            top: 4px;
        }

        &.extra {
            &:after {
                right: 4px !important;
                top: 6px !important;
            }
        }
    }
    .module-section a {
        &:before {
            margin: 5px 5px 0;
            position: absolute;
            right: 4px;
        }

        &.extra {
            &:before {
                right: 0px !important;
                top: 2px !important;
            }
        }
    }



    .module-section a.cross-link-small::before {
        background-color: #f90000;
        content: '';
        height: 82%;
        top: 0;
        transform-origin: top center;
        width: 2px;

        position: absolute;
        width: 1px;
        margin: 5px -14px 0;
    }

    .module-section a.cross-link-small::after {
        border-right: 1px solid #f90000;
        border-top: 1px solid #f90000;
        content: '';
        height: 7px;
        position: absolute;
        width: 7px;
        right: -11px;
        top: 4px;
    }

    .logos-wrapper a {
        margin-right: 30px;
    }

}

@media screen and (max-width: 960px) {
    .module-section {
        max-width: 700px;
    }
}

@media screen and (max-width: 860px) {
    .module-section {
        max-width: 600px;
    }
}

.dayOne__talk {
    a {
        text-decoration: none;
        color: black;

        &:after {
            display: none;
        }

        &:before {
            display: none;
        }
    }

    &.bold {
        .dayOne__talk-author {
            margin-bottom: 0;
        }
    }
}


