.flex-wrapper-col {
  display: flex;
  flex-direction: column;
}

.flex-wrapper-row {
  display: flex;
  flex-direction: row;
}

.flex-wrapper {
  display: flex;
  flex-direction: row;
}

.bullets__bullet {
  &::before {
    background: url("../assets/images/bullet-arrow.png") no-repeat top right;
    background-size: contain;
    content: "";
    width: 20px;
    transform: translateY(5px);
    height: 23px;
    display: block;
    margin-bottom: -23px;
  }

  &--text {
    padding-left: 30px;
    margin-bottom: 0.5rem;
  }
}

.table-layout.col-1 {
  column-count: 1;
}

@media screen and (max-width: 768px) {
  .flex-wrapper {
    flex-direction: column;
  }

  .flex-wrapper > .organizers__wrapper-inner {
    width: 100%;
  }
}



