.module-section {

    &__title-wrapper {
        margin-bottom: 10px;
        position: relative;
    }

    &__time {
        padding-left: 10px;
        color: $font-color--secondary;
        padding-top: 12px;
    }

    &__time-title {
        padding-left: 15px;
        color: $font-color--secondary;
    }
}

.arrow_current--paper {
    &::before {
        background: url("../assets/images/paper02.png") no-repeat top right;
        background-size: contain;
        content: '';
        width: 30px;
        height: 30px;
        position: relative;
        transform: translateY(10px);
        display: inline-block;
    }
}

.arrow_current--lightning {
    &::before {
        background: url("../assets/images/paper01.png") no-repeat top right;
        background-size: contain;
        content: '';
        width: 30px;
        transform: translateY(10px);
        height: 30px;
        position: relative;
        display: inline-block;
    }
}

.text--mono {
    font-family: 'suisse-intl-mono';
    font-size: 13px;
}

.abstracts {
    margin-top: 3rem;

    @media screen and (max-width: 550px) {
        margin-top: 1rem;
    }
        
    &__desc {
        margin-left: 50px;
        padding-right: 100px;
        line-height: 26px;
    }
}

.abstract {
    padding-right: 10vw;
    height: 200px;

    @media screen and (max-width: 1080px) {
        padding-right: 0vw;
    }

    span {
        line-height: 26px;
    }
}

@media (max-width: 900px) {
    .abstract {
        padding-right: 0vw;
    }
}

em {
    font-style: normal;
}

.abstracts-wrapper {
    display: flex;

    .abstracts {
        width: 100%;

        .abstracts__desc {

            @media screen and (max-width: 768px) {
                padding-right: 20%;
            }

            @media screen and (max-width: 450px) {
                padding-right: 0;
            }
            
        }
    }
    
    @media screen and (max-width: 768px) {
        flex-direction: column;
    }
}



