.keynote {
    // border: 2px solid blue;
    overflow: hidden;
    width: 100%;

    &__item {
        // border: 2px solid red;
        box-sizing: border-box;
        float: left;
        margin-bottom: 50px;
        position: relative;
        width: 40%;

        @media screen and (max-width: 950px) {
            width: 100%;
        }

        &:nth-child(odd) {
            margin-right: 10%;
        }

        &-speaker {
            align-items: baseline;
            // border: 2px solid green;
            display: flex;
            justify-content: space-between;
            margin-bottom: 15px;
            position: relative;

            &--name {
                background-color: $bg-color;
                // border: 2px solid pink;
                font-weight: bold;
                padding-right: 10px;

                &::before {
                    // background-color: rgba(255, 0, 0, 1);
                    // bottom: 13%;
                    // content: "";
                    // height: 75px;
                    // mix-blend-mode: lighten;
                    // position: absolute;
                    // right: 0;
                    // width: 75px;
                }

                // &::after {
                //     background-color: $line-color;
                //     top: 80%;
                //     content: "";
                //     height: 1px;
                //     left: 0;
                //     position: absolute;
                //     width: 100%;
                //     z-index: -1;
                // }
            }
            &--image {
                border-left: 15px solid $bg-color;
                height: 80px;
                margin-bottom: 11px;
                width: 80px;
            }
        }

        &-text {

        }
    }
}



