.highlight {
    font-weight: bold;
    text-align:center;
    width:100px;
}
.mark {
    font-family: 'suisse-intl-mono';
    white-space: nowrap;
}

.mt3-r {
    @media screen and (max-width: 768px) {
        margin-top: 3rem;
    }
}

.conditional_linebreak {
    @media screen and (max-width: 480px) {
        display: none;
    }
}

.oh {
    overflow: hidden;
}

.mw100 {
    max-width: 100%;
}

.module {
    &-section {
        // border: 2px solid black;
        // height: 85vh;
        margin-left: auto;
        margin-right: auto;
        max-width: 840px;

        @media screen and (max-width:1200px) {
            margin-right: 40px;
        }


        &:first-of-type {
            margin-top: 80vh;


        @media screen and (max-width: 550px) {
            margin-top: 90vh;
        }
        }

        &__wrapper {
            display: flex;
            flex-direction: column;
            align-items: baseline;
            margin-bottom: 3rem;
            font-size: 1.1rem;
            margin-top: 10rem;  
            transform: translateX(-2px);

            @media screen and (max-width: 500px) {
                margin-top: 4rem;
                margin-bottom: 2rem;
            }
        }

        a {
            color: $font-color--secondary;
            padding-right: 25px;
            position: relative;
            // text-decoration: none;
            white-space: nowrap;
        
            &::after {
                border-right: 2px solid $font-color--secondary;
                border-top: 2px solid $font-color--secondary;
                content: '';
                height: 8px;
                position: absolute;
                right: 4px;
                top: 6px;
                width: 8px;
            }
            &::before {
                background-color: $font-color--secondary;
                content: '';
                height: 92%;
                margin: 7px 5px 0;
                position: absolute;
                right: 0;
                top: 0;
                transform: translate(50%) rotate(45deg);
                transform-origin: top center;
                width: 2px;
            }
        }
        
        &__title {
            font-weight: normal;
            font-size: 38px;
            letter-spacing: 0.8px;
            margin-bottom: 0rem;
            line-height: 48px;

            @media screen and (max-width: 500px) {
                font-size: 34px;
            }

            &--secondary {
                color: $font-color--secondary;
                font-weight: normal;
                font-size: 38px;
                // position: absolute;
                text-transform: lowercase;

                @media screen and (max-width: 500px) {
                    font-size: 32px;
                }
            }


            @media screen and (max-width: 400) {
                width: 100%;
                float: left;
                margin-bottom: 1px;
            }
        }

        &__text--large p {
            font-size: 24px;
            line-height: 36px;
        }

        &__subtitle {
            letter-spacing: 0.05em;
            transform: translateX(-1px);
            font-weight: normal;
            color: $font-color--secondary;
            font-size: 24px;
            margin-top: 0;
            margin-bottom: 1.5rem;

            @media screen and (max-width: 550px) {
                margin-bottom: 0.75rem;
            }

        }

        &__start {
            a {
                padding-right: 16px; 
                &::after {
                    border-right: 1px solid $font-color--secondary;
                    border-top: 1px solid $font-color--secondary;
                    height: 6px;
                    right: 5px;
                    width: 6px;
                }
                &::before {
                    height: 72%;
                    margin: 7px 6px 0;
                    width: 1px;
                }
            }
        }

    @media screen and (max-width: 1080px) {
        max-width: 800px;
    }

    @media screen and (max-width: 800px) {
        padding-left: 10px;
        padding-right: 10px;
    }

    @media screen and (max-width: 950px) {
        max-width: 600px;
        margin-right: 50px;
    }

    @media screen and (max-width: 768px) {
        margin-right: auto;
    }
    
    }
}

.bullets {
    &__wrapper {
        margin-bottom: 15rem;
        display: flex;
        flex-direction: column;
        margin-bottom: 3rem;

        a {
            &::after {
                border-right: 1px solid $font-color--secondary;
                border-top: 1px solid $font-color--secondary;
                height: 6px;
                right: 14px;
                width: 6px;
            }
            &::before {
                height: 72%;
                margin: 7px 15px 0;
                width: 1px;
            }
        }
    }
}



