.table-layout {
  // border: 2px solid blue;
  column-count: 2;
  column-gap: 40px;
  cursor: default;
  width: 100%;

  @media screen and (max-width: 768px) {
    column-count: 1;
  }
  a {
    text-decoration: none;
    color: black;
    &:before {
      display: none;
    }
    &:after {
      display: none;
    }
  }

  &__item {
    // border: 2px solid pink;
    align-items: center;
    backface-visibility: hidden;
    cursor: default;
    // border-bottom: 1px solid $line-color;
    display: inline-flex;
    justify-content: space-between;
    line-height: 45px;
    transition: color 0.1s;
    transform: translate3d(0, 0, 0);
    width: 100%;

    &--first {
      // border: 2px solid blue;
      font-size: 16px;
      overflow: hidden;
      position: relative;
      text-transform: capitalize;
      width: 60%;

      &::before {
        background-color: $line-color;
        content: '';
        position: absolute;
        height: 1px;
        right: 10px;
        top: 50%;
        transition: transform 0.2s;
        transform-origin: right;
        width: 100%;
        z-index: -1;
      }

      &::after {
        background-color: $font-color--secondary;
        content: '';
        position: absolute;
        height: 1px;
        right: 10px;
        top: 50%;
        width: 100%;
        z-index: -2;
      }

      &-text {
        background-color: white;
        padding-right: 15px;
      }
    }
    &--second {
      // border: 2px solid red;
      font-size: 10px;
      line-height: 1.5em;
      font-family: 'suisse-intl-mono';
      letter-spacing: 0.4px;
      text-transform: uppercase;
      width: 40%;
      white-space: normal;
    }

    &:hover {
      color: $font-color--secondary;

      .table-layout__item--first::before {
        transform: scaleX(0);
      }
    }

    &--active {
      color: $font-color--secondary;
      .table-layout__item--first::before {
        transform: scaleX(0);
      }
    }
  }

  &__committee {
    margin-bottom: 3rem;
    margin-top: -0.5rem;

    @media screen and (max-width: 550px) {
      margin-bottom: 2rem;
    }

    &--local {
      cursor: default;
      line-height: 1.7em;
      .table-layout__item--first {
        &::before,
        &::after {
          display: none;
        }
      }
    }
  }

  &__dates {
    margin-top: 75px;

    .table-layout__item--first {
      width: 60%;
    }
  }
}



