.banner {
    background-color: $bg-color;
    border-bottom: 1px solid $line-color;
    display: flex;
    font-size: 20px;
    justify-content: space-between;
    left: 0;
    padding: 15px 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999;

    &__item {
        display: flex;
        justify-content: space-between;
        margin-left: auto;
        margin-right: auto;
        max-width: 900px;
        width: 100%;
        letter-spacing: 0.8px;

        @media screen and (max-width: 1080px) {
            max-width: 600px;
        }

        &--title {
            font-weight: bold;
        }
        &--hashtag {

        }

        &--highlight {
            color: $font-color--secondary;
        }
    }
}



