.baseline {
  background-image: linear-gradient(
    to bottom,
    #0ff 0,
    rgba(255, 255, 255, 0) 1px
  );
  background-repeat: repeat-y;
  background-size: 100% 1rem;
}

body {
  background-color: $bg-color;
  color: $font-color--primary;
  font-family: "suisse-intl", Helvetica, Arial, sans-serif;
  font-size: 16px;
  overflow-x: hidden;
}

p {
  margin: 0;
  line-height: 26px;
  letter-spacing: 0.3px;
}

.link {
  &:hover {
    &:before {
      background-color: $highlight-color;
      transition: background-color 0.15s;
    }
    &:after {
      border-right: 1px solid $highlight-color;
      border-top: 1px solid $highlight-color;
      transition: border-right 0.15s border-top 0.15s;
    }
    color: $highlight-color;
  }
}

.link--xl {
  &:hover {
    &:before {
      background-color: $highlight-color;
      transition: background-color 0.1s;
    }
    &:after {
      border-right: 2px solid $highlight-color;
      border-top: 2px solid $highlight-color;
      transition: border-right 0.15s border-top 0.15s;
    }
    color: $highlight-color;
  }
}

.mt2 {
  margin-top: 3rem;

  @media screen and (max-width: 550px) {
    margin-top: 1rem;
  }
}

.preload-mono {
  font-family: "suisse-intl-mono";
  font-weight: bold;
}

::selection {
  background: #ff9f9f;
}

.mt3 {
  margin-top: 3rem;
}

.mt15 {
  margin-top: 1.5rem;
}

.mb15 {
  margin-bottom: 1.5rem;
}

.mb3 {
  margin-bottom: 3rem;
}

.mb5 {
  margin-bottom: 0.5rem;
}

.mono {
  font-size: 10px;
  font-family: "suisse-intl-mono";
  letter-spacing: 0.4px;
  text-transform: uppercase;
  line-height: 15px;
  margin-top: 0.5rem;

  a {
    padding-right: 14px !important;
    padding-top: 4px;
  }

  &--subtext {
    margin-top: 7px;
  }
}



