.timeline {
    // border: 1px solid $font-color--primary;
    border-bottom: 1px solid $font-color--primary;
    height: 70px;
    left: 0;
    position: relative;
    width: 100%;

    &-indicator {
        background-color: $font-color--secondary;
        bottom: 0;
        display: none;
        height: 100%;
        left: 0;
        position: absolute;
        width: 1px;
    }

    &-marker {
        bottom: 0;
        font-size: 10px;
        left: 0; // get's overwritten by timeline.js
        line-height: 4em;
        font-family: 'suisse-intl-mono';
        height: 8px;
        position: absolute;
        text-align: right;
        text-transform: uppercase;
        transform: translateX(-50%);
        white-space: nowrap;
        width: calc(100% / 7 + 5px);

        &::before {
            background-color: $font-color--primary;
            content: '';
            height: 8px;
            left: 50%;
            position: absolute;
            top: 0;
            width: 1px;
        }
    }

    &-event {
        background-color: $font-color--primary;
        border-bottom: 3px solid $bg-color;
        border-top: 3px solid $bg-color;
        height: 5px;
        left: 0;
        overflow: hidden;
        position: absolute;
        top: 0;
        transition: border-color 0.2s;

        &::after {
            background-color: $font-color--secondary;
            content: '';
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            transform: scaleX(0);
            transform-origin: left;
            transition: transform 0.2s;
            width: 100%;
        }

        &--active {
            &::after {
                transform: scaleX(1);
            }
        }

        &:hover {
            &::after {
                background-color: $font-color--secondary;
                transform: scaleX(1);
            }
        }
    }

    &-lines {
        // border: 1px solid $font-color--primary;
        position: absolute;
        top: 60%;
        transform: translateY(-50%);
        width: 100%;

        &__line {
            // border: 1px solid $font-color--primary;
            background-color: $font-color--secondary;
            border-radius: 5px;
            position: absolute;
            height: 5px;
            top: 0;

            &:hover {
                background-color: $font-color--secondary;
            }

            &--call {
                background-color: $font-color--primary;
                left: 0;
                width: 50%;
            }
            &--notification {
                background-color: $font-color--primary;
                left: 50%;
                top: -15px;
                width: 1%;
            }
            &--registration {
                background-color: $font-color--primary;
                left: 55%;
                width: 10%;
            }
            &--conference {
                background-color: $font-color--primary;
                left: 80%;
                top: 0;
                width: 8%;
            }
            &--ieee {
                background-color: $font-color--primary;
                left: 78%;
                top: -15px;
                width: 3%;
            }
            &--exhibition {
                background-color: $font-color--primary;
                left: 80%;
                top: -30px;
                width: 20%;
            }
        }
    }
}



