.workshops {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 80px;

  @media screen and (max-width: 768px) {
    display: block;
  }
  &-row {
    width: 47%;

    @media screen and (max-width: 768px) {
      width: 100%;
    }
  }
  &__workshop {
    margin-bottom: 5rem;

    &-type {
      font-size: 10px;
      line-height: 1.5em;
      font-family: 'suisse-intl-mono';
      font-weight: normal;
      letter-spacing: 0.4px;
      position: relative;
      text-transform: uppercase;

      &.sold-out::before {
        content: 'sold out';
        left: 0;
        position: absolute;
        font-size: 10px;
        padding: 0.25rem;
        font-family: 'suisse-intl-mono';
        letter-spacing: 0.4px;
        text-transform: uppercase;
        color: white;
        background: red;
        top: -35px;
      }
    }
    &-title {
      font-weight: bold;
      letter-spacing: 0.3px;
      padding-right: 15%;
    }
    &-description {
      margin: 1rem 0;
    }
    &-team {
      font-size: 10px;
      line-height: 1.5em;
      font-family: 'suisse-intl-mono';
      letter-spacing: 0.4px;
      padding-right: 25%;
      text-transform: uppercase;
    }
  }
}



