.organizers {
  // border: 2px solid $helper-frame;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  display: flex;
  margin-bottom: 3rem;

  @media screen and (max-width: 550px) {
    margin-bottom: 2rem;
  }

  &__wrapper {
    // border: 2px solid darkblue;
    display: flex;
    flex-direction: row;

    @media screen and (max-width: 720px) {
      flex-direction: column;
    }

    &-inner {
      // border: 2px solid $font-color--secondary;
      width: 50%;

      @media screen and (max-width: 720px) {
        flex-direction: column;
        width: 50%;
      }

      @media screen and (max-width: 500px) {
        width: 100%;
      }

      &:first-of-type {
        padding-right: 30px;
      }
    }
  }

  &__organizer {
    &:first-of-type {
      margin-bottom: 1rem;
    }
    // border: 2px solid green;
    flex-direction: column;
    font-size: 16px;
    min-height: 55px;
    width: 70%;

    @media screen and (max-width: 720px) {
      width: 100%;
    }

    &:first-of-type {
      padding-right: 20px;
    }

    &--name {
      width: 100%;
      float: left;
      margin-bottom: 0.5rem;
    }

    &--institute {
      font-size: 10px;
      float: left;
      font-family: 'suisse-intl-mono';
      letter-spacing: 0.4px;
      text-transform: uppercase;
      line-height: 15px;
    }
  }
}

.about__text {
  font-size: 24px;
  width: 90%;
  margin-bottom: 3rem;

  p {
    line-height: 36px;
  }

  @media screen and (max-width: 550px) {
    width: 100%;
    margin-bottom: 2rem;
  }
}

svg:hover polygon {
  fill: $font-color--secondary;
  transition: fill 0.15s;
}

svg:hover path {
  fill: $font-color--secondary;
  transition: fill 0.15s;
}

svg:hover polyline {
  fill: $font-color--secondary;
  transition: fill 0.15s;
}

.logos-wrapper {
  margin-bottom: 3rem;
  a {
    color: white;
    margin-bottom: 5rem;
    padding-right: 30px;
    &:after {
      display: none;
    }
    &:before {
      display: none;
    }

    &:nth-child(1) {
      // background-color: red;
      svg {
        height: 70px;
        transform: translateY(13%);

        @media screen and (max-width: 450px) {
          height: 60px;
        }
      }
    }
    &:nth-child(2) {
      // background-color: yellow;
      svg {
        height: 67px;
        transform: translateY(20%);

        @media screen and (max-width: 450px) {
          height: 55px;
        }
      }
    }
    &:nth-child(3) {
      // background-color: blue;
      svg {
        height: 53px;
        transform: translateY(6%);

        @media screen and (max-width: 450px) {
          height: 43px;
        }
      }
    }
    &:nth-child(5) {
      // background-color: gray;
      svg {
        height: 50px;
        transform: translateY(19%);

        @media screen and (max-width: 450px) {
          height: 45px;
        }
      }
    }
    &:nth-child(6) {
      // background-color: green;
      svg {
        height: 28px;

        @media screen and (max-width: 450px) {
          height: 25px;
        }
      }
    }

    svg {
      height: 50px;
      width: auto;
      margin-bottom: 30px;

      @media screen and (max-width: 450px) {
        height: 40px;
      }

      @media screen and (max-width: 400px) {
        height: 35px;
      }
    }
  }

  .logo {
    &-image {
      display: inline-block;
      max-height: 40px;
      margin-bottom: 35px;
      width: auto;

      &--first {
        display: inline-block;
      }
      &--second {
        display: none;
      }

      &--efre {
        margin-bottom: 28px;
        max-height: 50px;

        @media screen and (max-width: 450px) {
          max-height: 30px;
        }
      }
      &--cluster {
        @media screen and (max-width: 450px) {
          margin-bottom: 26px;
          max-height: 32px;
        }
      }
      &--zem {
        margin-bottom: 30px;
        max-height: 50px;
        @media screen and (max-width: 450px) {
          max-height: 36px;
        }
      }
    }

    &-link {
      &:hover {
        .logo-image--first {
          display: none;
        }
        .logo-image--second {
          display: inline-block;
        }
      }
    }
  }
}

.impressum {
  margin-bottom: 7rem;
  a {
    margin-right: 37px;

    &::after {
      border-right: 1px solid $font-color--secondary;
      border-top: 1px solid $font-color--secondary;
      height: 6px;
      right: 14px;
      width: 6px;
    }
    &::before {
      height: 72%;
      margin: 7px 15px 0;
      width: 1px;
    }
  }
}

.subsection-wrapper {
  a {
    &::after {
      border-right: 1px solid $font-color--secondary;
      border-top: 1px solid $font-color--secondary;
      height: 6px;
      right: 14px;
      width: 6px;
    }
    &::before {
      height: 72%;
      margin: 7px 15px 0;
      width: 1px;
    }
  }
  margin-bottom: 3rem;
}

.about-safari {
  &--logos {
    a svg {
      max-width: 100px;
    }
  }
}



