#field {
    // border: 2px solid $helper-frame;
    left: 0;
    overflow: hidden;
    position: absolute;
    top: 0;
    max-width: 100vw;
}

.field {
    &-title {
        font-family: 'suisse-intl';
        pointer-events: none;
        position: absolute;
        letter-spacing: 0.5px;
        top: 30%;
        z-index: 1;
        margin: 0;
        font-weight: normal;
        font-size: 2.5rem;
        line-height: 4rem;

        @media screen and (max-width: 1280px) {
            top: 30%;
            font-size: 2rem;
            line-height: 3rem;
        }

        @media screen and (max-width: 950px) {
            top: 15%;
            font-size: 2.25rem;
            line-height: 3.5rem;
        }

        @media screen and (max-width: 400px) {
            font-size: 2rem;
            line-height: 2.75rem;
        }

        &--mark {
            font-family: 'suisse-intl-mono';
            text-transform: uppercase;
            font-size: 0.8em;
            padding-left: 175px;
            position: relative;

            @media screen and (max-width: 1080px) {
                padding-left: 55px;
            }

            @media screen and (min-width: 1080px) and (max-width: 1200px) {
                padding-left: 125px;
            }

            @media screen and (max-width: 950px) {
                width: 2px;
                height: 30px;
                padding-left: 0px;
                top: 105px;
                padding-top: 240px;
            }

            &:before {
                background-color: black;
                content: '';
                height: 2px;
                margin: 2px 2px 0;
                position: absolute;
                right: 0;
                top: 45%;
                left: 5px;
                width: 150px;

                @media screen and (max-width: 952px) {
                    width: 2px;
                    height: 100px;
                }

                @media screen and (max-width: 1080px) and (min-width: 950px) {
                    width: 30px;
                }

                @media screen and (min-width: 1080px) and (max-width: 1200px) {
                    width: 100px;
                }

                // @media screen and (min-color-index:0) and (-webkit-min-device-pixel-ratio:0) and (min-resolution:.001dpcm) {
                //     top: 15%;
                // }

                

            }
        }
    }

    .line-break {
        &--1500 {
            display: none;
            @media screen and (min-width: 1500px) {
                display: static;
            }
        }
    }
}

br.line-break--950 {
    display:none;
}

@media screen and (max-width: 950px) {
    br.line-break--950 {
        display: block;
    }
}

.intro-safari {
    &--title {

        @media all and (min-width: 1441px) {
            width: 75%;
        }

        @media all and (max-width: 1440px) {
            width: 79%;
        }

        @media all and (max-width: 1340px) {
            width: 82%;
        }

        @media all and (max-width: 960px) {
            width: 70%;
        }
    }
    &:before {
        top: 15%;
    }
}



