.header-wrapper {
  font-size: 10px;
  font-family: 'suisse-intl-mono';
  letter-spacing: 0.4px;
  text-transform: uppercase;
  line-height: 15px;
  margin-bottom: 1rem;
  display: flex;
  justify-content: start;

  .header__time {
    padding-left: 0;
    width: 82px;

    @media screen and (max-width: 860px) {
      width: 80px;
    }

    @media screen and (max-width: 625px) {
      width: 79px;
      padding-left: 0;
    }
  }

  .header__day {
    width: 80%;
  }
}

.wrapper-outer {
  display: flex;
  flex-direction: row;

  @media screen and (max-width: 860px) {
    flex-direction: column;
  }
}

.dayOne {
  &-wrapper {
    display: flex;
    flex-direction: column;
    width: 50%;
    padding-right: 2rem;
    margin-bottom: 60px;

    &.zero {
      @media screen and (max-width: 1080px) {
        width: 388px;
      }
      @media screen and (max-width: 960px) {
        width: 48%;
      }

      @media screen and (max-width: 860px) {
        width: auto;
      }

      .dayOne__talk.administrative {
        .dayOne__talk-wrapper {
          transform: translateX(0px);

          @media screen and (max-width: 1080px) {
            transform: translateX(0px);
          }

          @media screen and (max-width: 960px) {
            transform: translateX(0px);
          }
        }
      }


    }

    @media screen and (max-width: 860px) {
      width: auto;
    }

    &:nth-of-type(1) {
      @media screen and (max-width: 860px) {
        margin-bottom: 5rem;
      }
    }
  }

  &__talk {
    cursor: pointer;
    padding: 0.5rem 0;
    display: flex;
    justify-content: space-between;
    transition: all 0.15s linear;
    position: relative;
    justify-content: flex-start;

    @media screen and (max-width: 625px) {
      padding-top: 0.5rem;
      padding-bottom: 0.5rem;
      padding-left: 0;
      padding-right: 0;
    }

    a {
      color: rgba(0, 0, 0, 0.9);
      text-decoration: none;

      &::before,
      &:after {
        content: none;
      }
    }

    &.keynote {
      &:after {
        content: 'Keynote';
        position: absolute;
        font-size: 10px;
        padding: 0.25rem;
        font-family: 'suisse-intl-mono';
        letter-spacing: 0.4px;
        right: 8px;
        text-transform: uppercase;
        color: white;
        background: red;
      }
    }

    &.dinner {
      position: relative;

      &::after {
        position: absolute;
        left: 64px;
        top: 10px;
        // -webkit-transform: translateX(-312px) translateY(-20px);
        content: '';
        width: 8px;
        height: 14px;
        background: url('../assets/images/dinner.svg') no-repeat top right;
        letter-spacing: 0.4px;
        text-transform: uppercase;
        line-height: 15px;
        margin-bottom: 1rem;

        @media screen and (max-width: 1080px) {
          left: 61px;
        }

        @media screen and (max-width: 960px) {
          left: 63px;
        }

        @media screen and (max-width: 860px) {
          left: 64px;
        }

        @media screen and (max-width: 625px) {
          left: 64px;
        }

        
      }

      .dayOne__talk-author {
        margin-bottom: 0;
      }
    }

    &.lightning {
      position: relative;

      &.last {
        margin-bottom: 1rem;

        &:before {
          height: 60%;
        }
      }

      .dayOne__talk-wrapper {
        .dayOne__talk-title {
          display: none;
        }
        .dayOne__talk-author {
          margin-bottom: 0;
        }
      }

      &::before {
        border-left: 1px solid red;
        height: 100%;
        position: absolute;
        content: '';
        transform: translateX(65px);

        @media screen and (max-width: 1080px) {
          transform: translateX(64px);
        }

        @media screen and (max-width: 960px) {
          transform: translateX(67px);
        }

        @media screen and (max-width: 860px) {
          transform: translateX(67px);
        }

        @media screen and (max-width: 625px) {
          transform: translateX(66px);
        }

        @media screen and (max-width: 380px) {
          // transform: translateY(-20px) translateX(55px);
          // -webkit-transform: translateX(-540px) translateY(-20px);
          transform: translateX(62px);
        }
      }
    }

    &.lightning-title ~ .lightning:nth-sibling(0) {
      margin-bottom: 2rem;
    }

    &.lightning-title {
      margin-top: 2rem;
      position: relative;

      @media screen and (max-width: 860px) {
        padding-top: 1.75rem;
      }

      .dayOne__talk-wrapper {
        .dayOne__talk-title {
          display: none;
        }
      }

      &::after {
        position: absolute;
        transform: translateY(-24px) translateX(53px);
        left: 8px;
        // -webkit-transform: translateX(-312px) translateY(-20px);
        content: '';
        width: 117px;
        height: 14px;
        background: url('../assets/images/lightning-talks.svg') no-repeat top
          right;
        letter-spacing: 0.4px;
        text-transform: uppercase;
        line-height: 15px;
        margin-bottom: 1rem;

        @media screen and (max-width: 1080px) {
          transform: translateY(-20px) translateX(57px);
          // -webkit-transform: translateX(-296px) translateY(-20px);
          left: 4px;
        }

        @media screen and (max-width: 960px) {
          // transform: translateY(-20px) translateX(55px);
          // -webkit-transform: translateX(-248px) translateY(-20px);
          left: 7px;
        }

        @media screen and (max-width: 860px) {
          // transform: translateY(-20px) translateX(64px);
          // -webkit-transform: translateX(-522px) translateY(-20px);
          left: 7px;
        }

        @media screen and (max-width: 625px) {
          // transform: translateY(-20px) translateX(55px);
          // -webkit-transform: translateX(-540px) translateY(-20px);
          left: 4px;
        }

        @media screen and (max-width: 380px) {
          // transform: translateY(-20px) translateX(55px);
          // -webkit-transform: translateX(-540px) translateY(-20px);
          left: 0px;
        }
      }
    }

    &.administrative {
      cursor: default;

      &:hover {
        background: none;
      }

      .dayOne__talk-wrapper {
        .dayOne__talk-author {
          font-weight: normal;
          margin-bottom: 0rem;
        }
      }

      &:hover {
        background: white;
      }
    }

    &:hover {
      background: #fafafa;
      transition: background 0.15s ease;

      .dayOne__talk-author {
        color: red;
        transition: color 0.15s linear;
      }

      .dayOne__talk-time {
        color: red;
        transition: color 0.15s linear;
      }
    }

    &-abstract {
      display: none;
      letter-spacing: 0.3px;
      line-height: 150%;
    }

    &-title {
      font-size: 14px;
      letter-spacing: 0.3px;
      line-height: 150%;
    }

    &-image-wrapper {
      display: none;
    }

    &-author {
      font-size: 14px;
      font-weight: bold;
      letter-spacing: 0.3px;
      margin-bottom: 0.5rem;
      transition: color 0.15s linear;
    }

    &-affiliations {
      display: none;
    }

    &-allauthors {
      display: none;
    }

    &-time {
      width: 78px;
      font-size: 10px;
      float: left;
      font-family: 'suisse-intl-mono';
      letter-spacing: 0.4px;
      text-transform: uppercase;
      line-height: 15px;
      padding-top: 3px;
      transition: color 0.15s ease;

      @media screen and (max-width: 1080px) {
        width: 75px;
      }

      @media screen and (max-width: 960px) {
        width: 78px;
      }

      @media screen and (max-width: 860px) {
        width: 80px;
      }

      @media screen and (max-width: 625px) {
        width: 80px;
      }
    }

    &-wrapper {
      width: 80%;

      @media screen and (max-width: 960px) {
        width: 72%;
      }

      @media screen and (max-width: 860px) {
        width: 80%;
      }
    }
  }

  &__talk.active {
    background: #fafafa;

    &.lightning-title {
      .dayOne__talk-wrapper {
        .dayOne__talk-title {
          display: block;
        }
      }
    }

    &.lightning {
      .dayOne__talk-wrapper {
        .dayOne__talk-title {
          display: block;
        }

        .dayOne__talk-author {
          margin-bottom: 0.5rem;
        }
      }
    }

    .dayOne__talk-abstract {
      display: block;
      font-size: 14px;
      letter-spacing: 0.3px;
      line-height: 150%;
    }

    .dayOne__talk-image-wrapper {
      display: block;
      margin-bottom: 2rem;
      margin-top: 2rem;
      img {
        max-width: 100%;
      }
    }

    .dayOne__talk-title {
      font-size: 20px;
      margin-bottom: 1rem;
      letter-spacing: 0.3px;
    }

    .dayOne__talk-affiliations {
      display: block;
      font-size: 10px;
      float: left;
      font-family: 'suisse-intl-mono';
      letter-spacing: 0.4px;
      text-transform: uppercase;
      line-height: 15px;
    }

    .dayOne__talk-allauthors {
      margin-top: 1rem;
      display: block;
      font-size: 10px;
      float: left;
      font-family: 'suisse-intl-mono';
      letter-spacing: 0.4px;
      text-transform: uppercase;
      line-height: 15px;
    }
  }
}



