.photo {
  &-documentation {
    cursor: pointer;
    background-image: url('./images/photos.svg');
    background-position: left top;
    background-repeat: no-repeat;
    height: 34px;
    width: 160px;

    &--exhibition {
      margin-left: 30px;
      margin-top: 0px;
    }
  }
}

.slb {
  &Overlay {
    animation: slbOverlay 0.1s;
    background-color: white;
    opacity: 1;
  }
  &ContentOuter {
    padding: 0;
  }
  &ImageWrap {
    animation: slbEnter 0.2s;

    &:after {
      box-shadow: none;
    }
  }
  &LoadingText {
    color: transparent;

    &:after {
      content: 'Loading ';
      color: $font-color--secondary;
      font-size: 14px;
      left: 0;
      position: absolute;
      top: 0;
      white-space: nowrap;
    }
  }
  &Image {
    padding: 32px 0;
  }
  &Caption {
    color: $font-color--primary;
    letter-spacing: 0.3px;
    line-height: 1.5em;
    font-family: 'suisse-intl-mono';
    font-size: 10px;
    text-align: left;
    text-transform: uppercase;
  }
  &Arrow {
    position: absolute;
    top: 50%;
    margin-top: -5em;
    width: 5em;
    height: 10em;
    opacity: 0.7;
    text-indent: -999em;
    overflow: hidden;
    outline: none;

    &::before {
      transform: translateY(-50%) rotate(45deg);
    }
    &::after {
      transform: translateY(-50%) rotate(-45deg);
    }

    &::before,
    &::after {
      background-color: $font-color--secondary;
      content: '';
      position: absolute;
      height: 2px;
      top: 50%;
      margin: 0;
      width: 20px;
    }

    &.prev:before,
    &.prev:after {
      border: none;
      transform-origin: left;
      left: 20px;
      right: auto;
    }
    &.next:before,
    &.next:after {
      border: none;
      transform-origin: right;
      right: 20px;
      left: auto;
    }
  }
  &CloseBtn {
    animation: slbEnter 0.3s;
    color: transparent;
    line-height: 20px;
    position: absolute;
    height: 30px;
    right: 25px;
    top: 25px;
    text-align: right;
    outline: none;
    user-select: none;

    &:hover {
      opacity: 1;

      &::before {
        color: $font-color--secondary;
      }
    }

    &::before {
      color: $font-color--primary;
      content: 'close';
      font-size: 16px;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
    }
  }
  &ContentOuter {
    position: static;
  }
}



