.navigation {
    // border: 2px solid gray;
    display: inline-block;
    font-family: 'suisse-intl-mono';
    left: 0;
    position: fixed;
    top: 26vh;
    text-transform: uppercase;
    z-index: 99;

    &__header {
        display: none;
        font-family: 'suisse-intl';
        font-size: 30px;
        font-weight: normal;
        text-transform: lowercase;

        &--secondary {
            color: $font-color--secondary;
        }

        @media screen and (max-width: 768px) {
            display: block;
            margin-bottom: 12vh;
            margin-top: 4vh;
            padding-left: 40px;
        }
    }

    &__hashtag {
        bottom: 15px;
        cursor: default;
        display: block;
        font-family: 'suisse-intl-mono';
        font-size: 16px;
        left: 47px;
        // letter-spacing: 0.05em;
        position: fixed;
        text-transform: uppercase;
        transform: rotate(-90deg);
        transform-origin: left;
        z-index: 99;

        &--link {
            text-decoration: none;
            color: $font-color--primary;
            transition: color .1s linear;
            &:hover {
                color: $font-color--secondary;
                transition: color .1s linear;
            }
        }

        @media screen and (max-width: 768px) {
            display: none;

            &--active {
                display: block;
            }
        }
        
    }

    &-list {
        // border: 2px solid yellow;
        list-style-type: none;
        margin: 0;
        padding: 0;

        &__item {
            // border: 2px solid green;
            padding-left: 40px;
            position: relative;
            font-size: 16px;

            @media screen and (max-width: 768px) {
                font-size: 24px;
            }

            &::before {
                backface-visibility: hidden;
                background-color: $font-color--primary;
                content: '';
                height: 1px;
                left: 0;
                position: absolute;
                top: 50%;
                transform: scaleX(1) translateY(-50%);
                transform-origin: left;
                transition: transform .2s;
                width: 10px;
            }

            &:hover {
                font-weight: bold;

                &::before {
                    transform: scaleX(3) translateY(-50%);
                }
            }
            
            &-link {
                color: $font-color--primary;
                text-decoration: none;
                // letter-spacing: 0.05em;
                line-height: 40px;

                @media screen and (max-width: 768px) {
                    font-size: 20px;
                }
                
            }
            &.active {
                
                a {
                    color: $font-color--secondary;
                }

                &::before {
                    background-color: $font-color--secondary;
                    transform: scaleX(3) translateY(-50%);
                }
            }
        }
    }

    &-button {
        display: none;
    }

    @media screen and (max-width: 768px) {
        background-color: rgba(255, 255, 255, 1);
        transform: translateX(-100%);
        height: 100vh;
        width: 100%;

        &--visible {
            transform: translateX(0) translateY(0vh);
            height: 100%;
            opacity: 1;
            transition: opacity 0.1s;
            top: 0;
            background: $bg-color;
        }

        &-button {            
            cursor: pointer;
            bottom: 25px;
            display: inline-block;
            height: 40px;
            left: 50%;
            position: fixed;
            transform: translate(-50%, 0);
            transition: all 1s;
            width: 40px;
            z-index: 99;

            &__inner {
                display: block;
                height: 100%;
                width: 100%;

                &::before {
                    background-color: $font-color--secondary;
                    content: '';
                    height: 2px;
                    left: 0;
                    position: absolute;
                    top: 50%;
                    transform: translateY(400%);
                    transition: transform .2s;
                    width: 100%;
                }
                &::after {
                    background-color: $font-color--secondary;
                    bottom: 50%;
                    content: '';
                    height: 2px;
                    left: 0;
                    position: absolute;
                    transform: translateY(-400%);
                    transition: transform .2s;
                    width: 100%;
                }
            }

            &::after {
                background-color: $font-color--secondary;
                content: '';
                height: 2px;
                left: 0;
                position: absolute;
                top: 50%;
                transform: scaleX(0.7) translate(0, -50%);
                transition: transform .2s;
                transform-origin: left;
                width: 100%;
                z-index: -1;
            }

            &:hover {
                &::after {
                    transform: scaleX(1) translate(0, -50%) rotate(0);
                    transform-origin: left;
                }
            }

            &--active {
                &::after {
                    transform: scaleX(1) translate(0, -50%) rotate(45deg);
                    transform-origin: center;
                }

                .navigation-button__inner {
                    &::before {
                        transform: translateY(-50%) rotate(-45deg);
                    }
                    &::after {
                        transform: translateY(50%) rotate(-45deg);
                    }
                }

                &:hover {
                    &::after {
                        transform: scaleX(1) translate(0, -50%) rotate(45deg);
                        transform-origin: center;
                    }
                }
            }
        }
    }
}



