.vimeo-wrapper {
    z-index: 10000000;
    position: fixed;
    background: white;
    width: 100%;
    height: 100%;
    display: none;
    overflow: hidden;
    flex-direction: column;

    &.active {
        display:flex;   
    }
    
    iframe {
        top:30px;
        left:0;
        width:100%;
        height:80%;
        margin: 0 auto;
    }
}

.dayOne__talk.active {
    .btn-video {
        display:block;
    }
}

.dayOne__talk {
    .btn-video {
        display:none;
    }
}

@keyframes fadeInFromNone {
    0% {
        display: none;
        opacity: 0;
    }

    1% {
        display: block;
        opacity: 0;
    }

    100% {
        display: block;
        opacity: 1;
    }
}

.module-section > a {
    &:before {
        display: none;
    }

    &:after {
        display: none;
    }
}

#video-exit {
    z-index: 10000000;
    float: right;
    text-align: right;
    cursor: pointer;
    margin-top: 25px;
    margin-bottom: 20px;
    margin-right: 25px;

    &:hover {
        color: red;
    }
}

.btn-video {
    content: '';
    width: 124px;
    height: 34px;
    background: url('../assets/images/video.svg') no-repeat top right;
}

.btn-manual {
    cursor: pointer;
    content: '';
    width: 170px;
    height: 34px;
    background: url('../assets/images/manual.svg') no-repeat top right;
}

.btn-videos {
    cursor: pointer;
    content: '';
    width: 131px;
    height: 34px;
    background: url('../assets/images/videos.svg') no-repeat top right;
    margin-bottom: 20px;
}

.wrapper_iframes {
    width: 100%;
    height: 100%;
    top: 0px;
    position: fixed;
    display: none;
    z-index: 100000000;

    &.active {
        display: block;
        animation: fadeInFromNone 0.25s ease-out;
    }
}


