.canvas-wrapper {
  // border: 2px solid #0000ff;
  // left: 0;
  // position: absolute;
}

.col {
  column-count: 2;

  img {
    width: auto;
    height: 595px;
  }

  p {
    margin-bottom: 1rem;
  }

  span {
    p {
      font-size: 10px;
      font-family: "suisse-intl-mono";
      text-transform: uppercase;
      line-height: 160% !important;
      letter-spacing: 0.4px;
    }
  }

  &--directions {
    position: relative;

    .maps {
      img {
        &:nth-of-type(1) {
          display: block;
        }
        &:nth-of-type(2) {
          display: none;
        }
      }

      &:hover {
        img {
          &:nth-of-type(1) {
            display: none;
          }
          &:nth-of-type(2) {
            display: block;
          }
        }
      }
    }

    @media screen and (min-width: 901px) {
      p {
        float: left;
        width: 25%;
      }
      .maps {
        img {
          width: 74%;
        }
      }
    }
  }
}

@media screen and (max-width: 900px) {
  .col {
    column-count: 1;
  }

  .col.img {
    column-count: 2;

    img {
      width: auto;
      height: 400px;
    }

    span {
      p {
        width: 100%;
        font-size: 10px;
        font-family: "suisse-intl-mono";
        text-transform: uppercase;
        line-height: 160% !important;
        letter-spacing: 0.4px;
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .col.img {
    column-count: 1;

    img {
      width: auto;
      height: 400px;
    }
  }
}



